import { BigNumber, ethers } from 'ethers';
import { IRewardConfig } from '../models/ClaimConfig';
import { EthereumService } from './EthereumService';
import { MessagingService } from './MessagingService';
import { Utils } from './Utils';

export class RewardService {
  walletAddress?: string;
  contract?: ethers.Contract;
  balance: string | 0 = 0;
  gasPrice?: string | number;

  constructor(
    public config: IRewardConfig,
    private messagingService: MessagingService,
    private ethereumService: EthereumService
  ) {
    this.walletAddress = this.ethereumService!.walletData.walletAddress;
    this.contract = new ethers.Contract(this.config.contractAddress, this.config.contractAbi, ethereumService.signer);

    this.setupEventHandlers();
  }

  async update(): Promise<any> {
    var work = [this.getClaimBalance(), this.ethereumService!.provider!.getGasPrice()];

    return Promise.all(work).then(response => {
      this.balance = Utils.formatUnits(response[0], 0);
    });
  }

  async getClaimBalance(): Promise<any> {
    if (!this.contract) {
      return;
    }

    var walletAddress = this.ethereumService.walletData.walletAddress;

    var merkle = this.config!.claimMerkle.claims[walletAddress];

    if (!merkle) {
      return;
    }

    const claimed = await this.contract.isClaimed(merkle.index);

    if (claimed) {
      return BigNumber.from(0);
    } else {
      return merkle.amount;
    }
  }

  claim() {
    var walletAddress = this.ethereumService.walletData.walletAddress;

    if (!this.contract) {
      this.messagingService.errors.next({
        message: `Error: unable to load claim contract`,
        error: 'Unable to load claim contract'
      });
      return Promise.reject('Error: unable to load claim contract');
    }

    if (this.balance <= 0) {
      this.messagingService.errors.next({
        message: `Error: nothing to claim`,
        error: 'Nothing to claim'
      });
      return Promise.reject('Error: nothing to claim');
    }

    var merkle = this.config!.claimMerkle.claims[walletAddress];

    if (!merkle) {
      this.messagingService.errors.next({
        message: `Error: address not found in merkle root`,
        error: 'Address not found in merkle root'
      });
      return Promise.reject('Error: address not found in merkle root');
    }

    return this.contract.claimLootBox(merkle.index, merkle.proof, merkle.amount).then(
      (response: any) =>
        this.messagingService.events.next({
          message: `Reward claim is pending`,
          pending: true
        }),
      (error: any) =>
        this.messagingService.errors.next({
          message: `Error during claim : (${Utils.getContractErrorMessage(error)})`,
          error: error
        })
    );
  }

  removeEventHandlers() {
    if (this.contract) {
      this.contract!.removeAllListeners();
    }
  }

  setupEventHandlers() {
    this.removeEventHandlers();

    var update = (message: string) =>
      this.update().then(() => {
        this.messagingService.events.next({ message: message });
      });

    if (this.contract) {
      this.contract!.on(
        {
          address: this.ethereumService.walletData.walletAddress,
          topics: [ethers.utils.id('Claimed(uint256,address,uint256)')]
        },
        (index, account, amount) => {
          if (account === this.ethereumService.walletData.walletAddress) {
            update(`Reward claimed`);
          }
        }
      );
    }
  }
}
